require("capability/es5");

var capability = require("capability");

var polyfill;
if (capability("Error.captureStackTrace"))
    polyfill = require("error-polyfill/lib/v8");
else if (capability("Error.prototype.stack"))
    polyfill = require("error-polyfill/lib/non-v8");
else
    polyfill = require("error-polyfill/lib/unsupported");

module.exports = polyfill();